.popup__container {
  position: fixed;
  inset: 0;
  width: 430px;
  height: 332px;
  background-color: white;
  margin: auto;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, .15);
  border-radius: 10px;
}

@media (max-width: 540px) {
  .popup__container {
    width: 282px;
    height: 327px;
  }
}